<div id="selected-set-main"><div class="header"><div class="title">Reaction Set</div><div class="action-button-holder"><CopyButton :textToCopy="fullUrl" icon="share" buttonText="Shareable Link"></CopyButton><button :disabled="!reactionIds.length" @click="showDownloadResults=true">Download Reaction Set</button></div></div><div class="selected-set" v-if="!loading &amp;&amp; reactions.length"><ReactionCard v-for="reaction in reactions" :reaction="reaction" :isSelectable="false" :isSelected="false"></ReactionCard></div><div class="no-results" v-else-if="!loading &amp;&amp; !reactions?.length"><div class="title">There was an issue fetching your selected reactions.</div></div><div class="loading" v-else><LoadingSpinner></LoadingSpinner></div><DownloadResults :reactionIds="reactionIds" :showDownloadResults="showDownloadResults" @hideDownloadResults="showDownloadResults=false"></DownloadResults></div>